.contact{
    width: 100%;
    height: 100%;
    position: relative;
    margin-top: 5%;
    padding-top: 10%;
}
h1{
    color: rgb(87, 85, 85);
    text-transform: uppercase;
    margin-bottom: 5%;
}
.container_contact{
    max-width: 1200px;
    background-color: rgba(65, 105, 225, 0.849);
    height: 100%;
    margin: auto;
    display: flex;
    border-radius: 20px;
    flex-wrap:wrap;
}
.form{
    flex: 50%;
    display: grid;
    align-items: center;
    text-align: center;
    border-radius: 20px 0  0 20px;
}
input[type='text' ]{
    min-width: 450px;
    padding: 10px 20px;
    font-size: 1.1rem;
    margin-top: 20px;
    border-radius: 25px;
    outline: none;
}
input[type='email' ]{
    width: 450px;
    padding: 10px 20px;
    font-size: 1.1rem;
    margin-top: 20px;
    border-radius: 25px;
    outline: none;
}
textarea{
    width: 450px;
    resize: none;
    font-size: 1.1rem;
    height: 250px;
    margin-top: 60px;
    border-radius: 25px;
    outline: none;
    padding: 10px 10px;
}
input[type='submit' ]{
    width: 450px;
    padding: 10px 20px;
    font-size: 1.3rem;
    margin-top: 20px;
    border-radius: 25px;
    letter-spacing: 1px;
    font-family: "Roboto Slab", serif;
    outline: none;
}
.map{
    flex: 50%;
    height: 600px;
    padding: 10px;
    border-radius:0 20px 20px 0;
}
iframe{
    border-radius: 25px;
}
