.container-small{
    max-width: 1255px;
    height: 100%;
    position: relative;
    top: 20%;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    grid-gap: 5px;
}
.container_card{
    flex: 1;
    min-width: 400px;
}
.container_card .link{
    max-width: 100%;
    width: 100%;
    max-height: 100%;
    height: 100%;
    object-fit: cover;
}
 .container_card .link > img{
    max-width: 100%;
    width: 100%;
    max-height: 100%;
    height: 100%;
    object-fit: cover;
}
