@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;300;400;500;600;700&family=Roboto:wght@300;400&display=swap");
.nav {
  width: 100%;
  height: 10vh;
  position: fixed;
  z-index: 10;
}
.sticky {
  background-color: rgb(29, 28, 28);
  transition: 0.3s ease-in-out;
  box-shadow: 0 0 15px black;
}
.nav_logo {
  position: absolute;
  left: 60px;
  font-size: 3rem;
  font-family: "Roboto Slab", serif;
  letter-spacing: 1px;
  color: white;
}
.pro {
  color: royalblue;
  border-bottom: 4px solid white;
}
.web {
  border-bottom: 4px solid royalblue;
}
.nav_item {
  display: flex;
  float: right;
  list-style: none;
  padding-right: 90px;
}
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap");
.nav_link {
  margin: 15px 10px;
  padding: 10px 20px;
  font-size: 14px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-family: "Open Sans", sans-serif;
  border-radius: 5px;
  transition: 0.5 ease-in-out;
}
.active {
  background-color: royalblue;
}
.nav_link:hover {
  background-color: royalblue;
}
.btn {
  display: none;
}
@media screen and (max-width: 1000px) {
  .nav_item {
    background-color: royalblue;
    width: 35%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    position: relative;
   top: 10vh;
    right: -100%;
    transition: 0.3s ease-in-out;
  }
  .nav_link:hover {
    background-color: rgb(3, 43, 165);
  }
  .nav_link {
    width: 100%;
    margin: 0;
    text-align: center;
    padding: 20px 0;
  }
  .nav_item_mobile {
    background-color: royalblue;
    width: 35%;
    height: 90vh;
    top: 10vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    position: absolute;
    right: 0;
    list-style: none;
  }
  .btn {
    display: block;
    position: absolute;
    top: 18px;
    right: 50px;
    background: transparent;
    border: none;
    z-index: 10;
  }
  .btn i.fas {
    font-size: 2.3rem;
    color: white;
  }
}
@media screen and (max-width: 769px) {
  .nav_item {
    background-color: royalblue;
    width: 70%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    position: relative;
    right: -100%;
    z-index: 10;
    transition: 0.3s ease-in-out;
  }
  .nav_link {
    width: 100%;
    margin: 0;
    text-align: center;
    padding: 20px 0;
  }
  .nav_item_mobile {
    background-color: royalblue;
    width: 60%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    position: absolute;
    right: 0;
    list-style: none;
    z-index: 10;
  }
}
