.Portfolio{
    width: 100%;
    height: auto;
    padding-top: 5%;
}
.Portfolio h1{
    color: rgb(87, 85, 85);
    margin-bottom: 10%;
    text-transform: uppercase;
}
.btn_container{
    margin: 5% auto;
    display: flex;
    justify-content: center;
    grid-column-gap: 15px;
}
.button_filter{
    padding: 10px 20px;
    font-size: 1.2rem;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 1px;
    background-color: royalblue;
    border: none;
    outline: none;
    color: white;
    box-shadow: 0 0 2px rgb(99, 99, 99);
    border-radius: 5px;
    transition: 0.3s ease-in-out;
}
.button_filter:hover{
    box-shadow: 0 5px 10px rgb(150, 148, 148);
}
@media screen and (max-width: 570px) {
    .btn_container{
        margin: 5% auto;
        display: flex;
        justify-content: center;
        grid-column-gap: 5px;
    }
    .button_filter{
        padding: 10px 13px;
        font-size: 1.1rem;
    }
}