.home_container {
  width: 100%;
  height: 100vh;
  background: url("../img/about-5.jpg");
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.context::after{
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.context {
  position: relative;
  top: 55%;
  left: 35%;
  transform: translate(-45%, -65%);
  padding: 10px 10px;
  width: 40%;
  text-transform: capitalize;
  border-bottom: 4px solid royalblue;
 text-align: center;
}
h3 {
  font-size: 1.8rem;
  letter-spacing: 1px;
  color: rgb(214, 212, 212);
  font-family: "Roboto Slab", serif;
}
h1 {
  color: white;
  font-size: 4rem;
  font-family: "Roboto Slab", serif;
  font-weight: 800;
}
.front {
  color: rgba(255, 255, 255, 0.877);
}
p {
  font-size: 18px;
  color: rgb(214, 212, 212);
  font-family: "Open Sans", sans-serif;
  margin-top: 40px;
  letter-spacing: 1px;
}
.icon {
  position: absolute;
  display: grid;
  top: 50%;
  transform: translateY(-50%);
  left: 2%;
}
i.fab {
  font-size: 1.5rem;
  margin-top: 40px;
  color: white;
  transition: 0.3s ease-in-out;
}
i.fa-instagram:hover {
  color: rgb(214, 43, 86);
}
i.fa-facebook-square:hover {
  color: rgb(27, 72, 209);
}
i.fa-youtube:hover {
  color: red;
}
i.fa-twitter:hover {
  color: skyblue;
}
i.fa-github:hover {
  color: rgb(80, 78, 78);
}
@media screen and (max-width: 900px) {
    .context{
        position: absolute;
        top: 54%;
        left: 50%;
        transform: translate(-46%, -50%);
        padding: 10px 10px;
        width: 70%;
        text-transform: capitalize;
        border-bottom: 4px solid royalblue;
    }
}
@media screen and (max-width:769px) {
    .icon{
        position: absolute;
        display: flex;   
        transform: translateY(-10%);
        top: 90%;
        transform: translateX(-50%);
        left: 50%;
    }
    i.fab{
        margin: 10px 20px;
    }
}