.service{
    position: relative;
    margin-top: 20%;
    background-color: rgba(255, 245, 245, 0.644);
    padding-bottom: 50px;
}
.service h1{
    color: rgb(87, 85, 85);
}
.container-box{
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: 100px auto;
    grid-gap: 10px;
    justify-content: center;
   
}
.box{
    max-width: 380px;
    padding: 10px 15px;
    background-color: white;
    border-radius: 4px;
    text-align: justify;
    transition: 0.3s ease-in-out;
}
.box:nth-child(2){
    box-shadow: 1px 15px 5px rgb(139, 135, 135);
}
.box:hover{
    box-shadow: 2px 15px 10px rgb(139, 135, 135);
}
.box .img{
    width: 100%;
    font-size: 80px;
    color: rgb(0, 0, 0);
}
.box_info h2{
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    font-size: 1.5rem;
    margin-top: 10px;
    color: rgb(102, 100, 100);
}
.box_info p{
    color: black;
    font-family: 'Open Sans', sans-serif;
    font-size: 1rem;
}
 i.fas-fa {
    font-size: 80px;
}
@media screen and (max-width: 1000px){
i.fas {
    font-size: 80px;
    color: rgb(0, 0, 0);
}
}