.skill_bar{
    position: relative;
    margin-top: 5%;
    width:  80%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 45px;
    box-sizing: border-box;

}
.bar_data{
    width: 550px;
    font-family: "Roboto Slab", serif;
}
.bar_data p{
    float: right;
    position: relative;
    bottom: 65px;
    color: black;
}
.bar_level{
    width: 100%;
    height: 9px;
    border: 1px solid black;
    padding: .5px;
    border-radius: 5px;
}
.bar_level::after{
    content: '';
    display: block;
    position: relative;
    width: 85%;
    height: 100%;
    background-color: royalblue;
    border-radius: 5px;
}
.react::after{
    content: '';
    display: block;
    position: relative;
    width: 70%;
    height: 99%;
    background-color: royalblue;
    border-radius: 5px;
}