.card {
  width: 80%;
  margin: auto;
  position: relative;
  top: 35%;
  display: flex;
  flex-wrap: wrap;
  padding: 10px 10px;
}
.grid {
  flex-basis: 50%;
  height: 550px;
}
.grid-1 {
  flex-basis: 50%;
}
.card_img {
 max-width: 500px;
 margin-top: 50px;
  height: 100%;
  box-shadow:  -50px 60px 5px 10px rgb(18, 19, 19);
}
.card_img img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-height: 100%;
}
.info {
  height: auto;
  margin-top: 10%;
  padding: 10px 10px;
}
.info h2 {
  font-size: 3.5rem;
  font-family: "Roboto Slab", serif;
  text-transform: uppercase;
  color: rgb(66, 66, 66);
  margin-top: 60px;
}
.info p {
  margin-bottom: 30px;
  font-family: "Roboto Slab", serif;
  font-size: 1.3rem;
  color: rgb(83, 81, 81);
}
.info .ptag {
  letter-spacing: 0.5px;
  font-size: 1.2rem;
  font-family: "Open Sans", sans-serif;
}
.about_btn {
  background-color: royalblue;
  position: relative;
  top: 50px;
  padding: 15px 20px;
  text-decoration: none;
  color: white;
  font-size: 1.3rem;
  text-transform: uppercase;
  border-radius: 5px;
  transition: 0.3s ease-in-out;
}
.about_btn:hover {
  box-shadow: 2px 2px 15px rgb(65, 63, 63);
}
@media screen and (max-width: 1027px) {
  .card {
    width: 100%;
    margin: 1% auto;
    display: flex;
    flex-wrap: wrap;
    padding: 20px 30px;
    grid-gap: 5px;
  }
  .flex-1 {
    flex-basis: 100%;
  }
  .grid-1 {
    padding: 10px 10px;
    text-align: center;
    
  }
  .info {
    width: 100%;
    height: 100%;
    position: relative;
    bottom: 50px;
  }
  .card_img{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media screen and (max-width: 1284px) {
  .card{
    width: 100%;
  }
}
@media screen and (max-width: 947px) {
  .info{
    position: relative;
    bottom: 10px;
  }
}